@import "font";
@import "color";
@import "reset";
@import "scrollbar";
@import "basic";
@import "variable";

body {
    background: #000;
    color: #fff;
    display: grid;
    gap: 1rem;
    grid-template-columns: 1fr 1fr 1fr;
}

.area {
    height: calc(100% - 2rem);
    padding: 1rem 1.5rem;
    border: 1px solid #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

p {
    margin: 0;
    padding: 0;
}

p.count-day::before {
    content: "Days: ";
    font-style: italic;
    font-weight: 700;
}
