@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--red: #e63946;--redDark: #a73040;--white: #f0f0f0;--gray: #3a3d40;--grayLight: #d0d2d3;--gray-light: #3a3d4022;--black: #181719;--blueLight: #45567d;--yellow: #c0ce41}*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body{color:#f0f0f0;color:var(--white);font-family:Poppins, sans-serif;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition:0.2s}body ul,body li,body a{text-decoration:none;color:inherit;list-style:none}body input,body textarea{font-family:inherit;outline:none;border:1px solid rgba(51,51,51,0.3)}::-webkit-scrollbar{width:12px;background-color:#2d2e30}::-webkit-scrollbar-button{display:none}::-webkit-scrollbar-thumb{border:2px solid #2d2e30;margin:0 auto;background-color:#5f6368;border-radius:5px}::-webkit-scrollbar-track{width:10px}body header{--height-header: 50px;--width-header: 100%;--size-menu: 30px}body #contact a{--size-brand: 4rem;--gap-social: 1rem}body{background:#000;color:#fff;display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:1fr 1fr 1fr}.area{height:calc(100% - 2rem);padding:1rem 1.5rem;border:1px solid #fff;display:flex;justify-content:center;align-items:center;flex-direction:column}p{margin:0;padding:0}p.count-day::before{content:"Days: ";font-style:italic;font-weight:700}

